.btn--blue {
  background: #65abd0; }
  .btn--blue:hover {
    background: #5093b7; }
  .btn--blue:active {
    background: #77bfe5; }

.btn--white {
  background: #fff; }
  .btn--white:hover {
    background: #5093b7; }
  .btn--white:active {
    background: #77bfe5; }

.btn--green {
  background: #24aa98; }
  .btn--green:hover {
    background: #1a9385; }
  .btn--green:active {
    background: #42c7b7; }

.next {
  padding: 1em 1.5em;
  position: relative;
  transition: 0.2s linear; }

.next--md:hover:after {
  border-left: 22px solid  #5093b7; }

.next--md:after {
  content: '';
  position: absolute;
  right: -44px;
  top: 0;
  border: 25px solid transparent;
  border-left: 22px solid #65abd0; }

.next--sm:hover:after {
  border-left: 22px solid  #5093b7; }

.next--sm:after {
  content: '';
  position: absolute;
  right: -41px;
  top: 0;
  border: 22px solid transparent;
  border-left: 22px solid #65abd0; }

.btn-group {
  display: inline-flex; }

.icon-divider-blue {
  position: relative; }

.icon-divider-blue:after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 2px;
  background: #609fc0; }

.icon-divider-green {
  position: relative; }

.icon-divider-green:after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 2px;
  background: #269e8e; }

input {
  border: none; }

.placeholder {
  font-size: 1.6rem;
  padding-top: 0.75em;
  padding-bottom: 0.75em;
  padding-left: 0.9em;
  color: #383737;
  outline: none; }

.placeholder--left-shift {
  padding-left: 2.5em; }

.placeholder::-webkit-input-placeholder {
  /* Chrome/Opera/Safari */
  color: #b8bfc6; }

.placeholder::-moz-placeholder {
  /* Firefox 19+ */
  color: #b8bfc6; }

.placeholder::-ms-input-placeholder {
  /* IE 10+ */
  color: #b8bfc6; }

