.form-button {
  font-size: 1.6rem;
  padding: 0.5em 1.1em; }

.form-button--select {
  font-size: 1.6rem;
  padding: 0.85em 2em 0.85em 0.8em; }

select {
  margin: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-appearance: none;
  -moz-appearance: none; }

.select-arrow {
  position: relative; }

.select-arrow::after {
  content: '›';
  position: absolute;
  top: 0.55em;
  right: 0.8em;
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg);
  color: white;
  font-size: 2rem;
  pointer-events: none; }

.item__value {
  width: 20%; }

.item__value-input {
